@import url("chart.css");
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  background-color: #F7F7F7;
  font-family: 'Inconsolata';
  font-size: 16px;
  margin: 0;
  padding: 0;
}
input, select, button, pre, code, text {
  font-family: 'Inconsolata';
  font-size: 14px;
}
input, select, button {
  height: 40px;
}
input:focus, select:focus, button:focus {
  outline: none;
}
input, select {
  border: 1px solid #CCCCCC;
  padding: 0 10px;
}
input, button {
  width: 100%;
}
input {
  margin: 0;
}
input:focus {
  background-color: #F7F7F7;
}
select {
  background: -webkit-linear-gradient(45deg, transparent 50%, black 50%), -webkit-linear-gradient(315deg, black 50%, transparent 50%);
  background-position: calc(100% - 15px) 50%, calc(100% - 10px) 50%;
  background-size: 5px 5px, 5px 5px;
  background-repeat: no-repeat;
  border-radius: 0;
  width: 200px;
  position: relative;
  -webkit-appearance: none;
}
button {
  background-color: #00AF00;
  color: white;
  border: none;
  display: block;
  position: relative;
}
button span {
  position: relative;
  z-index: 1;
}
button:before {
  content: '';
  background-color: rgba(0, 0, 0, 0.2);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: right;
      -ms-transform-origin: right;
          transform-origin: right;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
button:hover:before {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
}
code {
  color: #777777;
  background-color: #EEEEEE;
  border: 1px solid #CCCCCC;
  padding: 2px 5px;
}
.app {
  min-width: 900px;
  max-width: 1400px;
  padding: 0 60px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .app {
    padding: 0 20px;
  }
}
.header {
  margin: 30px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.header .logo {
  height: 50px;
  width: auto;
}
.content {
  margin-bottom: 30px;
}
.tabs {
  border: 1px solid #CCCCCC;
  border-bottom: none;
  margin: 0;
  padding: 0;
  display: inline-block;
  position: relative;
  top: 1px;
}
.tabs li {
  background-color: #EEEEEE;
  height: 40px;
  padding: 0 60px;
  cursor: default;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
  align-items: center;
}
.tabs li:hover {
  background-color: #E7E7E7;
}
.tabs li.active {
  background-color: white;
  font-weight: bold;
}
.section {
  background-color: white;
  border: 1px solid #CCCCCC;
  padding: 30px;
}
.filters {
  margin-bottom: 30px;
}
.metrics > svg {
  height: 0;
  display: block;
}
.metrics .current {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.metrics .current .item {
  border: 1px solid #CCCCCC;
  width: calc(33% - 15px);
  padding: 20px;
}
.metrics .current p {
  font-size: 22px;
  margin-top: 0;
  margin-bottom: 15px;
}
.metrics .charts {
  margin-top: 30px;
  position: relative;
}
.metrics .chart {
  font-size: 14px;
  border: 1px solid #CCCCCC;
  padding: 20px;
}
.metrics .chart + .chart {
  margin-top: 30px;
}
.metrics .chart p {
  max-width: 900px;
  text-align: center;
  margin: 0 auto 15px;
}
.metrics .chart.events p {
  margin-bottom: 30px;
}
.metrics .chart svg {
  margin: 0 auto;
}
.log .output {
  background-color: #F7F7F7;
  border: 1px solid #CCCCCC;
  padding: 10px;
}
.log .output pre {
  white-space: pre-wrap;
  margin: 0;
}
.log .output.info span {
  color: #437BD7;
}
.log .output.debug span {
  color: #D78043;
}
.log .output.error span {
  color: #D74343;
}
.settings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-between;
  justify-content: space-between;
}
.settings .form, .settings .details {
  width: calc(50% - 15px);
  border: 1px solid #CCCCCC;
  padding: 15px;
}
.settings .form p {
  line-height: 1.4;
  margin-top: 0;
}
.settings .form button {
  margin-top: 15px;
}
.settings .details {
  list-style: none;
  margin: 0;
}
.settings .details p {
  margin-top: 0;
  margin-bottom: 10px;
}
.settings .details li + li {
  margin-top: 20px;
}



