.metrics .chart svg text {
  fill: black !important;
}
.metrics .chart svg rect + text {
  text-anchor: end !important;
}
.metrics .chart svg rect:not(.eventchart-marker) ~ text {
  font-size: 14px !important;
  -webkit-transform: translateX(41px);
  -ms-transform: translateX(41px);
  transform: translateX(41px);
}
.metrics .chart svg rect.eventchart-marker ~ text,
.metrics .chart svg line[pointer-events] ~ text {
  -webkit-filter: url('#solid');
  filter: url('#solid');
}
.metrics .chart svg line[pointer-events] ~ g text {
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}
.metrics .chart svg line[pointer-events] ~ g text tspan:first-child {
  display: none;
}
.metrics .chart > div > div:last-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  margin: 15px 0 0 !important;
  pointer-events: none;
}
.metrics .chart > div > div:last-child * {
  opacity: 1 !important;
}
.metrics .chart > div > div svg {
  margin-right: 7px;
}



